import React from 'react';
import Brand from "../brand";
import Wrapper from "../wrapper";
import arrowIcon from "../../assets/icons/arrow-down.svg"
import searchIcon from "../../assets/icons/search.svg"
import bellIcon from "../../assets/icons/bell.svg"
import avatar from "../../assets/images/avatar.png"
import Menu from "../menu";
import { useNavigate } from "react-router-dom";

const Index = () => {
    const navigate = useNavigate();
    const logout = () => {
        navigate("/auth/logout");
    }
    return (
        <header className={' bg-white py-3.5'}>
            <Wrapper>
                <div className="grid grid-cols-12 items-center">
                    <div className="col-span-2">
                        <Brand/>
                    </div>
                    <div className="col-span-7">
                        <Menu/>
                    </div>
                    <div className="col-span-3 text-right flex justify-end items-center">
                        <div className={'flex items-center cursor-pointer mr-8 font-semibold'}>
                            <span></span>
                            <img src={arrowIcon} alt=""/>
                        </div>
                        <div className={'cursor-pointer mr-8'}>
                            <img src={searchIcon} alt=""/>
                        </div>
                        <div className={'cursor-pointer mr-8'}>
                            <img src={bellIcon} alt=""/>
                        </div>
                        <div onClick={logout} className="cursor-pointer flex items-center">
                            <img className={'mr-2'} src={avatar} alt="avatar"/>
                            <span className={'font-semibold text-secondary-300'}> .</span>
                            <img src={arrowIcon} alt=""/>
                        </div>
                    </div>
                </div>
            </Wrapper>
        </header>
    );
};

export default Index;