import React from 'react';
import {Link, NavLink} from "react-router-dom";

const Menu = () => {
    return (
        <ul className={'flex items-center'}>
            <li className={'menu__item group'}>
                <NavLink className={'menu__item_link'} to={'/'}></NavLink>
            </li>
            <li className={'menu__item group'}>
                <NavLink className={'menu__item_link'} to={'/dispensaries'}></NavLink>
                <ul className="absolute z-5 bg-white  rounded-b-xl invisible group-hover:visible  -left-6 -right-6 top-2/3 py-3">
                    <li className={'mb-1'}>
                        <NavLink className={'px-3 py-1 text-black block transition hover:text-primary'} to={'/dispensaries/outpatient-reports'}></NavLink>
                    </li>
                    <li className={'mb-1'}>
                        <NavLink className={'px-3 py-1 text-black block transition hover:text-primary'} to={'/dispensaries/organizations'}></NavLink>
                    </li>
                    <li className={'mb-1'}>
                        <Link className={'px-3 py-1 text-black block transition hover:text-primary'} to={'#'}>.</Link>
                    </li>
                </ul>
            </li>
            <li className={'menu__item group'}>
                <NavLink className={'menu__item_link'} to={'/hospitals'}></NavLink>
                <ul className="absolute z-5 bg-white  rounded-b-xl invisible group-hover:visible  -left-6 -right-6 top-2/3 py-3">
                    <li className={'mb-1'}>
                        <NavLink className={'px-3 py-1 text-black block transition hover:text-primary'} to={'/hospitals/inpatient-reports'}></NavLink>
                    </li>
                    <li className={'mb-1'}>
                        <NavLink className={'px-3 py-1 text-black block transition hover:text-primary'} to={'/hospitals/organizations'}></NavLink>
                    </li>
                    <li className={'mb-1'}>
                        <Link className={'px-3 py-1 text-black block transition hover:text-primary'} to={'#'}>. </Link>
                    </li>
                </ul>
            </li>
            <li className={'menu__item'}>
                <NavLink className={'menu__item_link'} to={'/references'}></NavLink>
            </li>
            <li className={'menu__item'}>
                <NavLink className={'menu__item_link'} to={'/settings'}></NavLink>
            </li>
        </ul>
    );
};

export default Menu;